// UserNamePasswordMobileMail  mixin

import defaultValidateMessage from './default_validate_message.js';

export default {
    inject: ['LoginInstance'],
    props: {
        rules: {
            type: [Object, Array],
            default () {
                const componentName = this.$options.name;
                return [
                    {
                        required: true,
                        message: defaultValidateMessage[componentName],
                        trigger: 'change'
                    }
                ];
            }
        },
        // 
        value: {
            type: String
        },
        // 
        name: {
            type: String,
            required: true
        },
        // 
        enterToSubmit: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            prop: ''
        };
    },
    methods: {
        handleChange (val) {
            this.LoginInstance.formValidate[this.prop] = val;
            this.$emit('on-change', val);
        },
        handleEnter () {
            if (this.enterToSubmit) this.LoginInstance.handleSubmit();
        },
        // 
        handleSetValue () {
            const $props = this.$props;
            //  value 
            if ($props.value) {
                this.LoginInstance.formValidate[this.prop] = $props.value;
            }
        },
        handleGetProps () {
            const $props = this.$props;
            //  name  Input  prop  LoginItem  Input 
            let name = $props.name;
            const defaultProps = {
                prefix: this.prefix,
                placeholder: this.placeholder,
                type: this.type,
                size: 'large',
                //  Formlogin.vue 
                value: this.LoginInstance.formValidate[this.prop]
            };

            if (name) defaultProps.name = name;

            return Object.assign(defaultProps, this.$attrs);
        }
    },
    render (h) {
        //  render  @on-submit render 
        // this.handleSetValue();
        const finalProps = this.handleGetProps();

        const $input = h('i-input', {
            props: finalProps,
            on: {
                input: this.handleChange,
                'on-enter': this.handleEnter
            }
        });

        const $formitem = h('FormItem', {
            props: {
                prop: this.prop,
                rules: this.rules
            }
        }, [$input]);

        return h('div', {
            attrs: {
                class: this.className
            }
        }, [$formitem]);
    },
    created () {
        const name = this.name;
        //  this.LoginInstance.formValidate['name'] = ''; 
        //  iView  Form  model 
        const formValidate = Object.assign({}, this.LoginInstance.formValidate);
        formValidate[name] = '';
        this.LoginInstance.formValidate = formValidate;
        this.prop = name;
        this.handleSetValue();
    }
};



